.btn {
  text-decoration: none;
  border-radius: 4px;
  border: 3px solid $colorSidebar;
  padding: 10px 40px;
  color: $colorSidebar;
  display: inline-block;
  transition: .3s ease;
  font-size: .9em;
  white-space: nowrap;
  &:hover {
    background-color: rgb(211, 211, 211, .2);
  }
}
